import React from "react";
import Logo from "../img/meam.gif"

function MeamHeader(){
    return(
        <header>
           <h1 style={{fontFamily: "Palatino Linotype", fontSize: 40, color: "white"}}>
              <img src={Logo} alt="Logo" style={{ width: 70, display: "inline-block", marginRight: 50,verticalAlign: "middle"}}/>
              Meam Generator App
            </h1>
        </header>
    )
}

export default MeamHeader;