import React from "react";

class MeamBody extends React.Component{
    constructor(){
        super();
        this.state={
            topText: "",
            bottomText: "",
            randomImage: "https://i.imgflip.com/26am.jpg",
            imageApi: []
        }
        this.handeler=this.handeler.bind(this);
        this.onSubmit=this.onSubmit.bind(this);
    }

    handeler(event){
        const {name, value}=event.target;
        this.setState({
            [name]: value
        })
    }

    onSubmit(event){
        event.preventDefault();
        const randNum=Math.floor(Math.random() *this.state.imageApi.length);
        const randomNumber=this.state.imageApi[randNum].url;
        this.setState({randomImage: randomNumber});
    }

    componentDidMount(){
        fetch("https://api.imgflip.com/get_memes")
            .then(response => response.json())
            .then(item => {
                const {memes} = item.data;
                this.setState({imageApi: memes})
            })
    }

    render(){
        return(
        <div>
            <br/>
            <form onSubmit={this.onSubmit}>
                <input className="formElement" name="topText" placeholder="Enter top text" value={this.state.topText} onChange={this.handeler}/>
                <input className="formElement" name="bottomText" placeholder="Enter bottom text" value={this.state.bottomText} onChange={this.handeler}/>
                <button className="formButton" >Change!!</button>
            </form>
            <br/>
            <div className="imageContainer">
                <div className="top-Text">{this.state.topText}</div>
                <img className="img" src={this.state.randomImage} alt="meme"/>
                <div className="bottom-Text">{this.state.bottomText}</div>
            </div>
        </div>
        )
    }
}

export default MeamBody;